import {AfterViewInit, Component, ViewChild} from '@angular/core';
import {MatPaginator} from '@angular/material/paginator';
import {MatSort} from '@angular/material/sort';
import {MatTableDataSource} from '@angular/material/table';

import itemList from '../../assets/json/item.json';

export interface ItemData {
  id: number;
  name: string;
  type: string
  description: string;
}

/**
 * @title Data table with sorting, pagination, and filtering.
 */
@Component({
  selector: 'item-list',
  styleUrls: ['item-list.component.css'],
  templateUrl: 'item-list.component.html',
})
export class ItemListComponent implements AfterViewInit {
  displayedColumns: string[] = ['id', 'name', 'type', 'description'];
  dataSource: MatTableDataSource<ItemData>;

  @ViewChild(MatPaginator) paginator!: MatPaginator;
  @ViewChild(MatSort) sort!: MatSort;

  constructor() {
    // Assign the data to the data source for the table to render
    this.dataSource = new MatTableDataSource();
    document.getElementsByTagName('mat-sidenav-content')[0].scrollTo(0, 0);
  }

  ngAfterViewInit() {
    const datas = itemList.root[''].filter((item: any) => {
      return typeof item !== 'string' && item['_']
    }).map((item: any) => {
      let addDescription = '';
      if (item['_'] === '') {
        if (item['_1'] === '0') {
          addDescription += ': ';
        } else if (item['_1'] === '1'){
          addDescription += ': ';
        } else if (item['_1'] === '2'){
          addDescription += ': ';
        } else if (item['_1'] === '3'){
          addDescription += ': ';
        } else if (item['_1'] === '4'){
          addDescription += ': ';
        } else if (item['_1'] === '5'){
          addDescription += ': ';
        } else if (item['_1'] === '6'){
          addDescription += ': ';
        } else if (item['_1'] === '7'){
          addDescription += ': ';
        }
        if (item['_2']) {
          addDescription += `: ${+item['_2'] / 10}`;
        }
      }
      return {
        id: item['_'],
        name: item['_'],
        type: item['_'],
        description: item['_'] ? addDescription + item['_'] : ''
      }
    });
    for (const data of datas) {
      this.dataSource.data.push(data);
    }
    this.dataSource.paginator = this.paginator;
    this.dataSource.sort = this.sort;
  }

  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();

    if (this.dataSource.paginator) {
      this.dataSource.paginator.firstPage();
    }
  }
}
