import { OnInit, Component } from '@angular/core';
import { ActivatedRoute } from '@angular/router';

import itemList from '../../assets/json/item.json';
import dropList from '../../assets/json/drop.json';
import servDropList from '../../assets/json/serv_drop.json';


export interface ItemData {
  id: number;
  name: string;
  type: string
  description: string;
}

/**
 * @title Data table with sorting, pagination, and filtering.
 */
@Component({
  selector: 'drop',
  styleUrls: ['drop.component.css'],
  templateUrl: 'drop.component.html',
})
export class DropComponent implements OnInit {

  id: any;
  items: any = [];
  prob!: number;
  adv_items: any = [];
  drop: any;

  constructor(
    private route: ActivatedRoute
  ) {
    document.getElementsByTagName('mat-sidenav-content')[0].scrollTo(0, 0);
  }

  ngOnInit () {
    this.id = this.route.snapshot.paramMap.get('id');
    if (this.isMatchServeDrop(this.id)) {
      this.drop = servDropList.root.drop.find((drop: any) => drop['_'] === this.id);
    } else {
      this.drop = dropList.root.drop.find((drop: any) => drop['_'] === this.id);
    }
    const factor = +this.drop['_factor'];
    const adv_factor = +this.drop['_adv_factor'];
    let sum_prob = 0;
    for (let i = 1; i <= 20; i++) {
      const id = this.drop['_item'+i];
      if (!id) continue;
      const item = itemList.root[''].find((item: any) => { return item['_'] === id; });
      const count = this.drop['_count'+i];
      const prob = +this.drop['_prob'+i];
      sum_prob += prob;
      if (item) {
        this.items.push({ id: item['_'], name: item['_'], count: count, description: item['_'], prob: factor && prob ? prob * 100 / factor : undefined });
      } else {
        if (id === '2224') this.items.push({ name: '', count: count, description: '', prob: factor && prob ? prob * 100 / factor : undefined});
        if (id === '2225') this.items.push({ name: '', count: count, description: '', prob: factor && prob ? prob * 100 / factor : undefined});
        if (7000 < +id && +id < 7209) this.items.push({ name: '', count: count, description: '', prob: factor && prob ? prob * 100 / factor : undefined});
        if (10701 < +id && +id < 10750) this.items.push({ name: '', count: count, description: '', prob: factor && prob ? prob * 100 / factor : undefined});
        if (31251 < +id && +id < 31287) this.items.push({ name: '', count: count, description: '', prob: factor && prob ? prob * 100 / factor : undefined});
        if (id === '10857') this.items.push({ name: '', count: count, description: '', prob: factor && prob ? prob * 100 / factor : undefined});
        if (id === '20061') this.items.push({ name: '', count: count, description: '', prob: factor && prob ? prob * 100 / factor : undefined});
        if (id === '20062') this.items.push({ name: '', count: count, description: '', prob: factor && prob ? prob * 100 / factor : undefined});
        if (id === '20063') this.items.push({ name: '', count: count, description: '', prob: factor && prob ? prob * 100 / factor : undefined});
        if (id === '20064') this.items.push({ name: '', count: count, description: '', prob: factor && prob ? prob * 100 / factor : undefined});
        if (id === '20065') this.items.push({ name: '', count: count, description: '', prob: factor && prob ? prob * 100 / factor : undefined});
      }
    }
    this.prob = sum_prob * 100 / factor;
    for (let i = 21; i <= 40; i++) {
      const id = this.drop['_item'+i];
      if (!id) continue;
      const item = itemList.root[''].find((item: any) => { return item['_'] === id; });
      const count = this.drop['_count'+i];
      const prob = +this.drop['_prob'+i];
      if (item) {
        this.adv_items.push({ id: item['_'], name: item['_'], count: count, description: item['_'], prob: adv_factor && prob ? prob * 100 / adv_factor : undefined });
      } else {
        if (id === '2224') this.adv_items.push({ name: '', count: count, description: '', prob: adv_factor && prob ? prob * 100 / adv_factor : undefined});
        if (id === '2225') this.adv_items.push({ name: '', count: count, description: '', prob: adv_factor && prob ? prob * 100 / adv_factor : undefined});
        if (7000 < +id && +id < 7209) this.adv_items.push({ name: '', count: count, description: '', prob: adv_factor && prob ? prob * 100 / adv_factor : undefined});
        if (10701 < +id && +id < 10750) this.adv_items.push({ name: '', count: count, description: '', prob: adv_factor && prob ? prob * 100 / adv_factor : undefined});
        if (31251 < +id && +id < 31287) this.adv_items.push({ name: '', count: count, description: '', prob: adv_factor && prob ? prob * 100 / adv_factor : undefined});
        if (id === '10857') this.adv_items.push({ name: '', count: count, description: '', prob: adv_factor && prob ? prob * 100 / adv_factor : undefined});
        if (id === '20061') this.adv_items.push({ name: '', count: count, description: '', prob: adv_factor && prob ? prob * 100 / adv_factor : undefined});
        if (id === '20062') this.adv_items.push({ name: '', count: count, description: '', prob: adv_factor && prob ? prob * 100 / adv_factor : undefined});
        if (id === '20063') this.adv_items.push({ name: '', count: count, description: '', prob: adv_factor && prob ? prob * 100 / adv_factor : undefined});
        if (id === '20064') this.adv_items.push({ name: '', count: count, description: '', prob: adv_factor && prob ? prob * 100 / adv_factor : undefined});
        if (id === '20065') this.adv_items.push({ name: '', count: count, description: '', prob: adv_factor && prob ? prob * 100 / adv_factor : undefined});
      }
    }
    console.log('items', this.items);
  }

  isMatchServeDrop (id: number) {
    const drop = dropList.root.drop.find((drop: any) => drop['_'] === id);
    const serv_drop = servDropList.root.drop.find((drop: any) => drop['_'] === id);
    for (let i = 1; i <= 40; i++) {
      if (drop['_item'+i] !== serv_drop['_item'+i]) return false;
      if (drop['_count'+i] !== serv_drop['_count'+i]) return false;
    }
    return true;
  }

}
